import React from 'react'
import styled, { keyframes } from 'styled-components'
import { Tag, Flex, Heading, Image , Text} from '@pancakeswap/uikit'
import { CommunityTag, CoreTag } from 'components/Tags'

export interface ExpandableSectionProps {
  lpLabel?: string
  lpLabel2?: string
  multiplier?: string
  isCommunityFarm?: boolean
  farmImage?: string
  tokenSymbol1?: string
  tokenSymbol2?: string
}

const Wrapper = styled(Flex)`
  svg {
    margin-right: 4px;
  }
`
const runningLightAnimation = keyframes`
0% {
  text-shadow: 0 0 1px #62d5a3, 0 0 2px #62d5a3, 0 0 3px #62d5a3, 0 0 4px #62d5a3;
}
50% {
  text-shadow: 0 0 1px #62d5a3, 0 0 2px #62d5a3, 0 0 3px #62d5a3, 0 0 4px #62d5a3;
}
100% {
  text-shadow: 0 0 1px #62d5a3, 0 0 2px #62d5a3, 0 0 3px #62d5a3, 0 0 4px #62d5a3;
} 
`;

const Title = styled(Heading)`
  animation: ${runningLightAnimation} 2s infinite; /* Alkalmazzuk az animációt */
  color: #fff;
`;

const MultiplierTag = styled(Tag)`
  margin-left: 4px;
`

const CardHeading: React.FC<ExpandableSectionProps> = ({
  lpLabel,
  multiplier,
  isCommunityFarm,
  farmImage,
  tokenSymbol1,
  lpLabel2,
}) => {
  return (
    <Wrapper justifyContent="space-between" alignItems="center" mb="12px">
      <Image src={`/images/farms/${farmImage}.png`} alt={tokenSymbol1}  width={90} height={90} />
      <Flex flexDirection="column" alignItems="flex-end">
        <Title mt="0px">{lpLabel.split(' ')[0]}</Title>
        <Flex flexDirection="column" alignItems="flex-end">
        <Text fontSize='13px' color="#F3BA2F"  mb="4px">{lpLabel2.split(' ')[0]}</Text>
        <Flex justifyContent="center">
          {isCommunityFarm ? <CommunityTag /> : <CoreTag />}
          <MultiplierTag variant="secondary">{multiplier}</MultiplierTag>
        </Flex>
      </Flex>
      </Flex>

    </Wrapper>
  )
}

export default CardHeading